import { _decorator, Component, Node, Sprite, Label } from 'cc';
const { ccclass, property } = _decorator;


import { C_Player } from "../player/C_Player";
import { C_Game } from "./C_Game";
import { ResourceMgr } from 'db://assets/core_tgx/base/ResourceMgr';
import { Font } from 'cc';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { isValid } from 'cc';
import { SpriteFrame } from 'cc';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
import { Aclip } from '../common/define';
import { UiMgr } from '../common/uiMgr';
import { GameMain } from '../common/GameMain';
import { Message } from '../common/Message';
import { SubGameMgr } from 'db://assets/module_basic/scripts/SubGameMgr';
import { SubGameDef } from 'db://assets/module_basic/scripts/SubGameDef';
import { sp } from 'cc';

@ccclass('VGameOver')
export class V_GameOver extends Component {
    @property(Node)
    private bg: Node = null;
    @property(Sprite)
    private failbg: Node = null;
    @property(Label)
    private txtGold: Label | null = null;
    @property(Node)
    private resultEffect: Node = null!;
    async init(res:boolean,goldStr:number) {
        this.resultEffect.active = false;
        let fontUrl:string;
        if(res){
            AudioMgr.inst.playOneShot(Aclip.win,1,ModuleDef.POKER_NIUNIU);
            fontUrl = "font/success/successFont";
            ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_NIUNIU,fontUrl, Font, (err, font: Font) => {
                if (err || !isValid(this)) {
                    return;
                }
                this.txtGold.font = font;
                this.txtGold.string = "+"+goldStr;
            })
            // this.failbg.active = false;

        }else{

            AudioMgr.inst.playOneShot(Aclip.lose,1,ModuleDef.POKER_NIUNIU);
            fontUrl = "font/fail/failFont";
            ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_NIUNIU,fontUrl, Font, (err, font: Font) => {
                if (err || !isValid(this)) {
                return;
                }
                this.txtGold.font = font;
                this.txtGold.string = ""+goldStr;
            })
            // this.bg.active = false;
        }
        this.playResultEffect(res);

        if(C_Game.instance.autoMatch)
        {
            //开启了自动匹配
            this.scheduleOnce(() => {
                this.onRestart();
            },3)
        }
    }
    //播放胜利失败动画
    private playResultEffect(result:boolean):void
    {
        var dragonSke:sp.Skeleton = this.resultEffect.getComponent(sp.Skeleton);
        dragonSke.node.active = true;
        if(result)
        {
            dragonSke.setAnimation(0, "Victory", false);
        }
        else
        {
            dragonSke.setAnimation(0, "Defeat", false);
        }
        
        // dragonSke.setCompleteListener((event) => {
            // dragonSke.node.active = false;
        // });
    }
    onDestroy():void
    {
        this.unscheduleAllCallbacks();
    }
    onClose(){
        UiMgr.closePanel(this);
    }
    onOut(){
        C_Game.instance.leave();
    }
    async onRestart(){
        this.unscheduleAllCallbacks();
        //重新匹配
        let matchGameAgainRet = await SubGameMgr.inst.MatchGameAgain(SubGameDef.POKER_NIUNIU,SubGameMgr.gameMgr.subType);
        if(matchGameAgainRet.isSucc)
        {
            UiMgr.closePanel(this);
        }
    }
}