import { _decorator } from 'cc';
import { C_Game } from "../game/C_Game";
import { C_Player } from "./C_Player";

export class D_Player {
//    /**uid */
    public uid: string;
//    /**昵称 */
    public nickname: string;
//    /**金币 */
    public money: number;
//    /**代金卷 */
    public voucher: number;
//    /**头像 */
    public head: string;
//    /**座位号 */
    public seat: number;
    private _pic: number;
    public isReady: boolean = false;
    public get pic(): number {
        return Number(this._pic);
    }
    public set pic(num: number) {
        this._pic = num;
    }
    constructor() {

    }
    init(uid: string, nickname: string, money: number, head: string, pic: number, seat: number = 0,isReady:boolean): void {
        this.uid = uid;
        this.nickname = nickname;
        this.money = money;
        this.head = head;
        this.pic = pic;
        this.seat = seat;
        this.isReady = isReady;
    }
//    /**是否是玩家本人 */
    public get isMe(): boolean {
        return C_Player.instance.myPlayer.uid == this.uid;
    }
//    /**是否是庄 */
    public isZhuang(): boolean {
        return C_Game.instance.zhuangSeat == this.seat;
    }
}


