import { _decorator, Vec3 } from 'cc';
export class Bezier{
   
    constructor() {

    }
//    /**二阶贝塞尔曲线（t为占总时间比例）*/
    public static twoBezier(p0:Vec3,p1:Vec3,p2:Vec3,t:number):Vec3{
        let x:number = Math.pow((1-t),2)*p0.x+2*t*(1-t)*p1.x+Math.pow(t,2)*p2.x;
        let y:number = Math.pow((1-t),2)*p0.y+2*t*(1-t)*p1.y+Math.pow(t,2)*p2.y;
        return new Vec3(x,y);
    } 
}

