import { _decorator } from 'cc';
import { D_Player } from "./D_Player";
import { UserMgr } from 'db://assets/module_basic/scripts/UserMgr';
export interface I_Player {
    uid: string, gold: number, seat: number, nickname: string, head: string,pic:number,isReady:boolean
}

export class C_Player{
    public static _instance: C_Player = null;

//    /**玩家信息 */
    private _myPlayer: D_Player;
    public players: D_Player[];
    
    public get myPlayer():D_Player{
        if(!this._myPlayer){
        this._myPlayer = new D_Player();
        }
        return this._myPlayer;
    }
    public set myPlayer(value:D_Player){
        this._myPlayer = value;
    }
    public static get instance():C_Player{
        if(!this._instance){
        this._instance = new C_Player();
        }
        return this._instance;
    }
    constructor() {

    }
    init(players:I_Player[]){
        console.log("游戏中玩家信息初始化")
        C_Player.instance.players = [];
        for(let i:number=0;i<players.length;i++){
            let player = players[i];
            let temp:D_Player = new D_Player();
            C_Player.instance.players.push(temp);
            temp.init(player.uid,player.nickname,player.gold,player.head,player.pic,player.seat,player.isReady);
            console.log("uid:"+player.uid);
            console.log("seat:"+player.seat);
            if(player.uid == UserMgr.inst.uid)
            {
                C_Player.instance.myPlayer = temp;
            }
        }
    }
    clearPlayers(){
        console.log("清空上局游戏中玩家数据")
        C_Player.instance.players = [];
    }
    playerLeave(id:string)
    {
        
        for(var i:number=0;i<C_Player.instance.players.length;i++)
        {
            if(this.players[i].uid==id)
            {
                this.players.splice(i,1);
                break;
            }
        }
    }
    playerEnter(player:I_Player){
        let temp:D_Player = new D_Player();
        temp.init(player.uid,player.nickname,player.gold,player.head,player.pic,player.seat,player.isReady);
        C_Player.instance.players.push(temp);
    }
    getPlayerBySeat(seat:number):D_Player{
        if(this.players){
            for(let i=0;i<this.players.length;i++){
                let player:D_Player = this.players[i];
                if(player.seat==seat){
                    return player;
                }
            }
        }
        return null;
    }
    getPlayerByID(id:string):D_Player{
        if(this.players){
            for(let i=0;i<this.players.length;i++){
                let player:D_Player = this.players[i];
                if(player.uid==id){
                    return player;
                }
            }
        }
        return null;
    }
}