
import { Animation, AnimationClip, Node, Sprite,SpriteAtlas, SpriteFrame, UITransform } from "cc";
import { ResourceMgr } from "db://assets/core_tgx/base/ResourceMgr";
import { ModuleDef } from "db://assets/scripts/ModuleDef";

class animManager {
    private static sing: animManager = null

    public static get ins(): animManager {
        if (this.sing == null) {
            this.sing = new animManager();
        }
        return this.sing;
    }

    private cache: { [url: string]: SpriteFrame[] } = {}

    private loadAnimRes(url: string) {
            if (this.cache[url]) return this.cache[url];
            return new Promise((resolve, reject) => {
                 ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_NIUNIU, "effect/" + url, SpriteAtlas, (err, SpriteAtlas: SpriteAtlas) => {
                    if (err) {
                        console.error("资源错误", err);
                        reject("error");
                        return;
                    }
                    var fs = SpriteAtlas.getSpriteFrames();
                    this.cache[url] = fs
                    resolve(fs);
                });
            });
        }

    /**清除缓存 */
    public clearCache()
    {
        this.cache = {}
    }
    async createAnimNode(url: string, speed: number = 1, isLoop: boolean = false) {
        let node = new Node()
        node.addComponent(Sprite)
        let anim = node.addComponent(Animation)
        let sps = await this.loadAnimRes(url) as SpriteFrame[];
        if (sps.length > 0) {
            // 获取 UITransform 组件来设置节点大小
            const uiTransform = node.addComponent(UITransform);
            const originalSize = sps[0].getOriginalSize();
            uiTransform.setContentSize(originalSize.width, originalSize.height);
        }
        
        /** 创建clip */
        let clip: AnimationClip = AnimationClip.createWithSpriteFrames(sps, sps.length);
        /** 设置clip属性 */

        sps.forEach((spr, index) => {
            let x = spr['_offset']['x']
            let y = spr['_offset']['y']

            clip.events.push({
                frame: speed / sps.length * index,
                func: "setOffSet",
                params: [x+"", y+"", index+""]
            })
        })
        clip.name = "anim_desk"
        clip.speed = speed
        clip.wrapMode = isLoop ? AnimationClip.WrapMode.Loop : AnimationClip.WrapMode.Normal;
        /** 将clip添加到animation上 */
        anim.defaultClip = clip
        anim.addClip(clip);
        /** 播放设置默认的动画 */
        return node
        // anim.play();
    }
}


export const AnimManager = animManager.ins;


export const enum AnimUrl {
    type = "type_",
    win = "win",
    lose = "lose",
    match = "match",
    choose = "choose"
}

export const Sleep = (ms: number) => {
    return new Promise(resolve => setTimeout(resolve, ms))
}



