import { _decorator } from "cc";
import { configMgr } from "../common/configMgr";
import { C_Game } from "./C_Game";
import { Component } from "cc";
import { Button } from "cc";
import { Node } from "cc";
import { ResourceMgr } from "db://assets/core_tgx/base/ResourceMgr";
import { ModuleDef } from "db://assets/scripts/ModuleDef";
import { SpriteFrame } from "cc";
import { isValid } from "cc";
import { Sprite } from "cc";
import { Label } from "cc";
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';

const { ccclass, property } = _decorator;

@ccclass
export class V_Operate extends Component {
    @property(Node)
    private node_operate1: Node = null;   
    @property(Node)
    private node_operate2: Node = null;   

    @property(Button)
    private btn0: Button = null;   
    @property(Button)
    private btn1: Button = null;    
    @property(Button)
    private btn2: Button = null;     
    @property(Button)
    private btn3: Button = null;     
    @property(Button)
    private btn4: Button = null;     
    @property(Button)
    private btn5: Button = null; 

    @property(Button)
    private btnBet0: Button = null;   
    @property(Button)
    private btnBet1: Button = null;    
    @property(Button)
    private btnBet2: Button = null;     
    @property(Button)
    private btnBet3: Button = null;     
    @property(Button)
    private btnBet4: Button = null;  

    /**type 0叫庄，1下注，2摊牌 */
    public set type(num:number){
        if(num == 0)
        {
            this.node_operate1.active = true;
            this.node_operate2.active = false;
        }
        else
        {
            this.node_operate1.active = false;
            this.node_operate2.active = true;
        }

        if(num == 0){

            this.btn0.node.getChildByName("Label").getComponent(Label).string = LanguageData.getLangByID("niuniu_10");
            if(C_Game.instance.zhuangList){
                let zhuangList = C_Game.instance.zhuangList;
                for(let i:number=1;i<=5;i++){
                    let btnNode:Node = this.getBtn(i);
                    if(i<=zhuangList.length){
                        btnNode.active = true;
                        let label:Label = btnNode.getChildByName("Label").getComponent(Label);
                        label.string = zhuangList[i-1] + "X"; 
                    }else{
                        btnNode.active = false;
                    }
                } 
            }
        }
        if(num == 1){

            if(C_Game.instance.betList){
                let betList = C_Game.instance.betList;
                for(let i:number=0;i<=4;i++){
                    let btnNode:Node = this.getBetBtn(i);
                    if(i<betList.length){
                        let label:Label = btnNode.getChildByName("Label").getComponent(Label);
                        label.string = betList[i] + ""; 
                        btnNode.active = true;
                    }else{
                        btnNode.active = false;
                    }
                }
            }
        }
    }


    private getBtn(i:number):Node{

        return this.node_operate1.getChildByName("btn"+i);
    }
    private getBetBtn(i:number):Node{

        return this.node_operate2.getChildByName("operateBetBtn"+i);
    }
}