import { _decorator, Component, Vec3 } from 'cc';
const { ccclass, property } = _decorator;
import { Bezier } from "../Utils/Bezier";
import { Node } from 'cc';
import { Label } from 'cc';
import { GameMain } from '../common/GameMain';
import { Message } from '../common/Message';

@ccclass('Call')
export class Call  extends Component {
    private _p0:Vec3 = new Vec3();
    private _p1:Vec3 = new Vec3();
    private _p2:Vec3 = new Vec3();
    public setPos(p0:Vec3,p1:Vec3,p2:Vec3){
        this._p0 = p0;
        this._p1 = p1;
        this._p2 = p2;
    }
    private _t:number=0;
    public get t():number{
        return this._t;
    }
    public set t(t:number){
        this._t = t;
        let pos:Vec3 = Bezier.twoBezier(this._p0,this._p1,this._p2,t);
        this.node.x = pos.x;
        this.node.y = pos.y;
        if(t==1){
            let str = ((this.node.getChildByName("label") as Node).getComponent(Label) as Label).string;
            GameMain.evt.emit(Message.flyCallOver,str);
            this.node.parent = null;
        }
    }
    public set num(value:number)
    {
        (this.node.getChildByName("label") as Node).getComponent(Label).string = value.toString() + "X";
    }
}
