    private showHistory() {
        let players: I_Player[] = [];
        let histroyInfo: H_GameInfo[] = C_Game.instance.histroyInfo;
        let score: number = Number(histroyInfo[0].room_money);
        this.score = score;
        for (let i: number = 0; i < histroyInfo.length; i++) {
            let temp: H_GameInfo = histroyInfo[i];
            players.push({ uid: temp.user_id, gold: Number(temp.init_money), seat: Number(temp.seat), nickname: temp.username, head: null, pic: Number(temp.pic),isReady:true })
        }
        C_Player.instance.init(players);
        this.bindSeat();
        for (let i: number = 0; i < histroyInfo.length; i++) {
            let vSeat: V_Seat = this.getVSeat(Number(histroyInfo[i].seat));
            vSeat.isFrame = (histroyInfo[i].is_landlord == "1");
            let cash_times: number = Number(histroyInfo[i].cash_times);
            if (!vSeat.isFrame) {
                vSeat.setRecord(score * cash_times, 1);
            }
            vSeat.setType(Number(histroyInfo[i].brand_result));
            vSeat.cardType = Number(histroyInfo[i].brand_times);
            let cards: any = JSON.parse(histroyInfo[i].poker);
            vSeat.setCards(cards);
            let goldChange: number = Number(histroyInfo[i].settle_money);
            vSeat.goldChange = goldChange;
        }
    }
    start() {
        C_Game.instance.init();
        PokerManager.inst.start();
    }
    onDestroy(): void {
        this.unscheduleAllCallbacks();
    }
    update(dt) {
        if (this._doStartCountdown)
        {
            this.doStartCountdown();
        }
        if (this._doFarmCountdown)
        {
            this.doFarmCountdown();
        }
        if (this._GameOverWait > 0)
        {
            this.doGameOverWait();
        }
    }
    private _txtScore: Label = null!;
    public set score(num: number) {
        if (!this._txtScore) {
            this._txtScore = ((this.node.getChildByName("score") as Node).getChildByName("txtScore") as Node).getComponent(Label) as Label;
        }
        this._txtScore.string = LanguageData.getLangByID("niuniu_11",num); 
    }
    private _txtCall: Label = null!;
    public set callNum(str: string) {
        if (!this._txtCall) {
            this._txtCall = ((this.node.getChildByName("score") as Node).getChildByName("call") as Node).getComponent(Label) as Label;
        }
        if(str == "")
        {
            this._txtCall.string = "";
        }
        else
        {
            this._txtCall.string = LanguageData.getLangByID("niuniu_12",str);
        }
    }
