    private _GameOverWait: number=0;
    private _goldChange: number=0;
    private async gameOver() {
        UiMgr.hidePopupView();
        let award: i_gov;
        let arr = [];
        for (let i: number = 0; i < C_Game.instance.awards.length; i++) {
            award = C_Game.instance.awards[i];
            if ((this.mySeat as V_Seat).seat == C_Game.instance.awards[i].seat) {
                C_Player.instance.myPlayer.money = award.gold;
                this._goldChange = award.win_gold;
                this.seatArr[0].setGold(C_Player.instance.myPlayer.money);
            }
            let seat: V_Seat = this.getVSeat(C_Game.instance.awards[i].seat) as V_Seat;
            if (!(this.getVSeat(C_Game.instance.awards[i].seat) as V_Seat).isFrame) {
                seat.setRecord(C_Game.instance.awards[i].user_scale * C_Game.instance.score, 1);
            }
            seat.goldChange = award.win_gold;
            if (!seat.isFrame) {
                arr.push([seat, award.win_gold]);
            } else {
                arr.unshift([seat, award.win_gold]);
            }

        }
        for (let i = 1; i < arr.length; i++) {
            let seat = arr[i][0];
            let win_gold = arr[i][1];
            if (Number(win_gold) > 0) {
                coinEffect.instance.create((arr[0][0] as V_Seat).headPos, (seat as V_Seat).headPos);
            } else {
                coinEffect.instance.create((seat as V_Seat).headPos, (arr[0][0] as V_Seat).headPos);
            }

        }
        this.result.removeAllChildren();
        let goldChange: number = this._goldChange;
        let res: boolean = goldChange > 0;
        if (res) {
            let node: Node = await AnimManager.createAnimNode(AnimUrl.win, 1, false)
            let effect: Animation = node.getComponent(Animation) as Animation;
            node.parent = this.result;
            node.active = true;
            effect.play("anim_desk");
        } else {
            let node: Node = await AnimManager.createAnimNode(AnimUrl.lose, 1, false)
            let effect: Animation = node.getComponent(Animation) as Animation;
            node.parent = this.result;
            node.active = true;
            effect.play("anim_desk");
        }
        tgx.AudioMgr.inst.audio.playEffect(Aclip.gold,ModuleDef.POKER_NIUNIU);
        this._GameOverWait = 1000;
    }
    private doGameOverWait(): void {
        let date: Date = new Date();
        let timer: number = date.getTime();
        let num: number = Math.floor(timer - C_Game.instance.tempTime);
        if (num > this._GameOverWait) {
            this._GameOverWait = 0;
            this.showGameOver();
        }
    }
    private showGameOver(): void {
        UiMgr.showTipsView(uiPanel.GAMEOVER, (node) => {
            let goldChange: number = this._goldChange;
            let res: boolean = goldChange > 0;
            (node.getComponent(V_GameOver) as V_GameOver).init(res, goldChange);
        });
    }
