    //发牌
    private onDealCards()
    {
        this.cardPool.removeAllChildren();
        let renShu = PokerManager.inst.roomData.userList.length;
        for (let i = 0; i < 5 * renShu; i++) {
            let card = instantiate(this.cardPrefab);
            card.setParent(this.cardPool);
            card.setPosition(0, 0);
            card.active = true;
        }
        //分牌
        let i = this.cardPool.children.length - 1;
        let countArr = [0, 0, 0, 0];
        let intervalId = setInterval(() => {
            let card = this.cardPool.children[i];
            let vSeat:V_Seat = this.seatArr[i % renShu]
            let toNode = this.seatArr[i % renShu].cards ;
            let isSelf = vSeat.isMe;

            let newVec2 = toNode.getWorldPosition();
            let vec2 = this.cardPool.getComponent(UITransform).convertToNodeSpaceAR(newVec2);
            let scale = isSelf ? 1 : 0.4;
            tween(card)
            .delay(0.05)
            .to(0.3, { position: vec2, scale: new Vec3(scale, scale, scale) })
            .call(() => {
                
                  card.active = false;
              })
            .start();
            i--;
            if (i < 0) {
                clearInterval(intervalId);
                //发牌结束
                this.onToTanPai();
            }
        },10);
    }
    private onToTanPai() {
        for (let i: number = 0; i < this.playerNum; i++) {
            this.seatArr[i].showCards();
        }
        //按顺序摊牌 todo
        this.processTanPaiIndex(this.seatArr.length - 1);
    }
