import { ModuleDef } from "db://assets/scripts/ModuleDef";
import { GameMain } from "./GameMain";
import { Message } from "./Message";
import { uiPanel } from "./uiMgr";
import { JsonAsset } from "cc";
import { ResourceMgr } from "db://assets/core_tgx/base/ResourceMgr";
import { isValid } from "cc";
import { error } from "cc";
import { SpriteFrame } from "cc";
import { Prefab } from "cc";
import { Font } from "cc";
import { SpriteAtlas } from "cc";
import { sp } from "cc";

export class configMgr {
    public static ChatTextArr: string[] = [];

    public static _instance: configMgr = null;
    public loaderOver: boolean;
    total: number;
    count: number;
    public static get instance(): configMgr {
        if (!this._instance) {
            this._instance = new configMgr();
        }
        return this._instance;
    }

    constructor() {

    }

    
    public onloadConfig() {
        this.total = 0;
        this.count = 0;
        this.loaderOver = false;

        this.loadChat();

        for (let key in uiPanel) {
            this.loadPanel(uiPanel[key]);
        }

        this.loadFont("font/success/successFont");
        this.loadFont("font/fail/failFont");
        this.loadFont("font/cardType/cardTypeFont");
        this.loadFont("font/counters/counters");
 

        for (let i = 1; i < 5; i++) {
            for (let j = 3; j < 17; j++) {
                if (j == 15) continue;
                this.loadPic(`images/poker_${i}_${j}`)
            }
        }

        for (let i = 0; i < 15; i++) {
            this.loadEffect("type_" + i)
            this.loadPic("images/type/type_"+i);
        }
        
        this.loadRoomEffect("res/room/chuji/ChuJi");
        this.loadRoomEffect("res/room/zhongji/zhongji");
        this.loadRoomEffect("res/room/gaoji/gaoji");
    }

    /**加载聊天配置 */
    private loadChat() {
        this.total++;
        this.doLoadChat();
    }
    private doLoadChat() {
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_NIUNIU, "json/chat", JsonAsset, (err, asset: JsonAsset) => {
            if (err || !isValid(this)) {
                return error(err);
            }
            configMgr.ChatTextArr = asset.json.chatList;
            this.count++;
            GameMain.evt.emit(Message.loadUpdate);
        })
    }
    /**加载图片 */
    private loadPic(url: string) {
        this.total++;
        this.doLoadPic(url);
    }
    private doLoadPic(url: string) {
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_NIUNIU, url+"/spriteFrame", SpriteFrame, (err, atlas: SpriteFrame) => {
            if (err || !isValid(this)) {
                return error(err)
            }
            this.count++;
            GameMain.evt.emit(Message.loadUpdate);
        })
    }

    private loadPanel(url: string) {
        this.total++;
        this.doLoadPanel(url);
    }
    private doLoadPanel(url: string) {
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_NIUNIU, "prefab/" + url, Prefab, (err, atlas: Prefab) => {
            if (err || !isValid(this)) {
                return error(err)
            }
            this.count++;
            GameMain.evt.emit(Message.loadUpdate);
        });
    }

    /**加载字体 */
    private loadFont(url: string) {
        this.total++;
        this.doLoadFont(url);
    }
    private doLoadFont(url: string) {
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_NIUNIU, url, Font, (err, atlas: Font) => {
            if (err || !isValid(this)) {
                return error(err)
            }
            this.count++;
            GameMain.evt.emit(Message.loadUpdate);
        })
    }

    /**加载特效 */
    private loadEffect(url: string) {
        this.total++;
        this.doLoadEffect(url);
    }
    private doLoadEffect(url: string) {
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_NIUNIU, "effect/" + url, SpriteAtlas, (err, atlas: SpriteAtlas) => {
            if (err) {
                return error(err)
            }
            this.count++;
            GameMain.evt.emit(Message.loadUpdate);
        });
    }
     /**加载特效 */
    private loadRoomEffect(url: string) {
        this.total++;
        this.doLoadRoomEffect(url);
    }

    private doLoadRoomEffect(url: string) {
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.BASIC, url, sp.SkeletonData, (err, atlas: sp.SkeletonData) => {
            if (err) {
                return error(err)
            }
            this.count++;
            GameMain.evt.emit(Message.loadUpdate);
        });
    }

}