import { _decorator, Component, Label, Sprite, Node } from 'cc';
import { AnimationClip } from 'cc';
const { ccclass, property } = _decorator;

import { Animation } from 'cc';
import { e_room_status_client } from '../common/define';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';

@ccclass('VClock')
export class V_Clock extends Component {
    @property(Label)
    private time: Label = null!;
    @property(Node)
    private clockBg: Node = null!;
    @property(Label)
    private tipsLable: Label = null!;
    private _state: Node = null!;
    private _aniPlay: boolean = false; //动画是否在播放中
    public setTime(num: number) {
        this.clockBgBn = num > 0;
        this.timeBn = num > 0;
        this.time.string = "" + num;
        if(num<=3)
        {
            if(!this._aniPlay)
            {
                let ani = this.clockBg.getComponent(Animation);
                let state = ani.getState(ani.defaultClip.name);
                state.setTime(0);
                ani.play();
                ani.defaultClip.wrapMode = AnimationClip.WrapMode.Loop;
                console.log("clock play");
                this._aniPlay = true;
            }
            
        }
        else
        {
            if(this._aniPlay)
            {
                let ani = this.clockBg.getComponent(Animation);
                ani.stop();
                this.clockBg.setRotationFromEuler(0, 0, 0)
                console.log("clock stop");
                this._aniPlay = false;
            }  
        }
    }
    public set clockBgBn(bn: boolean) {
        this.clockBg.active = bn;
        this.timeBn = bn;
    }
    public set timeBn(bn: boolean) {
        this.time.node.active = bn;
    }
    public set state(num: number) {
        // console.log("clock state:" + num)
        if (num == e_room_status_client.DengDai) {
            this.tipsLable.string = LanguageData.getLangByID("niuniu_2");
        }
        if (num == e_room_status_client.QiangZhuang) {
            this.tipsLable.string = LanguageData.getLangByID("niuniu_3");;
        }
        if (num == e_room_status_client.XiaZhu) {
            this.tipsLable.string = LanguageData.getLangByID("niuniu_4");;
        }
        if (num == e_room_status_client.goStart) {
            this.tipsLable.string = LanguageData.getLangByID("niuniu_5");;
        }
    }
}