//    /**房间id */
    public roomId: string ="";
//    /**房间状态 */
    public roomStatus: e_room_status_client=e_room_status_client.Normal;
//    /**下注倍数列表 [1,2,3,4,5] */
    public betList: number[]=[];
//    /**抢庄倍数列表 [1,2,3,4,5] */
    public zhuangList: number[]=[];
//    /**庄家的位置 */
    public zhuangSeat: number=0;
//    /**庄的倍数 */
    public zhuangScale: number=0;
//    // /**庄家的位置 -1 表示没定庄 */
//    // public zhuangMax:number;
//    /**抢庄倍数 */
    public scale: number=0;
    //是否不抢
