import { _decorator, Prefab, Node, Vec3 } from 'cc';
import { Call } from "./Call";
import { Coin } from "./Coin";
import { instantiate } from 'cc';
import { tween } from 'cc';
import { Label } from 'cc';

export class coinEffect {
    public static _instance: coinEffect;
    public static get instance(): coinEffect {
        if (!this._instance) {
            this._instance = new coinEffect();
        }
        return this._instance;
    }
    coin: Prefab = null!;
    call: Prefab = null!;
    parentNode: Node = null!;
    //    /**生产金币个数 */
    private num: number = 20;
    //    /**金币创建时间区间 */
    private createTime: number = 500;
    //    /**金币飞行时间 */
    private flyTime: number = 800;
    //    /**倍数飞行时间 */
    private flyCall: number = 800;
    //    /**贝塞尔曲线y轴浮动 */
    private change: number = 100;
    //    /**起始位置浮动 */
    private changePos: number = 10;
    private _timeOutArr: number[] = [];
    create(sPos: Vec3, ePos: Vec3) {
        for (let i = 0; i < this.num; i++) {
            let time: number = this.createTime * Math.random();
            this._timeOutArr.push(
                setTimeout(() => {
                    let coin: Coin = (instantiate(this.coin) as Node).getComponent(Coin) as Coin;
                    let sCPos: Vec3 = this.sCPos(sPos)
                    let cPos: Vec3 = this.centerPos(sCPos, ePos);
                    coin.setPos(sCPos, cPos, ePos);
                    coin.t = 0;
                    coin.node.parent = this.parentNode;
                    tween(coin).to(this.flyTime / 1000, { t: 1 }, {}).start()
                }, time)
            );
        }
    }
    createCall(sPos: Vec3, num: number) {
        let ePos = new Vec3(0, 0);
        let cPos: Vec3 = this.centerPos(sPos, ePos);
        let call:Call = instantiate(this.call).getComponent(Call) as Call;
        call.setPos(sPos, cPos, ePos);
        call.t = 0;
        call.node.parent = this.parentNode;
        call.num = num;
        tween(call).to(this.flyCall / 1000, { t: 1 }, {}).start()
    }
    private sCPos(pos: Vec3): Vec3 {
        let temp: Vec3 = new Vec3();
        temp.x = pos.x - this.changePos + 2 * this.changePos * Math.random();
        temp.y = pos.y - this.changePos + 2 * this.changePos * Math.random();
        return temp;
    }
    private centerPos(p1: Vec3, p2: Vec3): Vec3 {
        let pos: Vec3 = new Vec3();
        pos.x = p2.x - this.change + 2 * this.change * Math.random();
        pos.y = (p2.y - p1.y) / 2 + p1.y - this.change + 2 * this.change * Math.random();
        return pos;
    }
}



