    private async bindCardType() {
        this.scheduleOnce(() => {
            if (this.isMe) {
                AudioMgr.inst.playOneShot(this.niuArr[this._type],1,ModuleDef.POKER_NIUNIU);
            }
        },1);
        
    }
    get headPos(): Vec3 {
        let meInfo:Node = this.node.getChildByName("headPrefab") as Node;
        let pos = new Vec3();
        pos.x = this.node.x + meInfo.x;
        pos.y = this.node.y + meInfo.y;
        return pos;
    }

