import { _decorator, Component, Node } from 'cc';
import { GameMgr } from 'db://assets/module_basic/scripts/GameMgr';
import { RoomMgr } from 'db://assets/module_basic/scripts/RoomMgr';
import { SubGameDef } from 'db://assets/module_basic/scripts/SubGameDef';
import { SubGameMgr } from 'db://assets/module_basic/scripts/SubGameMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { configMgr } from './common/configMgr';
import { GameMain } from './common/GameMain';
import { Message } from './common/Message';
const { ccclass, property } = _decorator;


@ccclass('PokerNiuniuGameMgr')
export class PokerNiuniuGameMgr extends GameMgr {
    private static _inst: PokerNiuniuGameMgr ;
    public static get inst(): PokerNiuniuGameMgr  {
        if (!this._inst) {
            this._inst = new PokerNiuniuGameMgr ();
        }
        return this._inst;
    }

    constructor() {
        super();
        this._gameType = SubGameDef.POKER_NIUNIU;
        this._entryScene = { name: 'lobby_poker_niuniu', bundle: ModuleDef.POKER_NIUNIU };
        this._gameScene = { name: 'game_poker_niuniu', bundle: ModuleDef.POKER_NIUNIU };
        this._loadingScene = { name: 'subgame_loading', bundle: ModuleDef.BASIC };
    }
    public override uinit()
    {
        GameMain.evt.targetOff(this);
    }
    /**
     * 加载资源
     */
    public override async preLoadRes(processFun:Function):Promise<boolean> {
        
        return new Promise((resolve, reject) => {
            function loadUpdate(){
                let total:number = configMgr.instance.total;
                let count:number = configMgr.instance.count;
        
                if(count>=total){
                    processFun(1);
                    resolve(true);
                    GameMain.evt.off(Message.loadUpdate,loadUpdate,this);
                    return;
                }
                processFun(count/total);
            }
            GameMain.evt.on(Message.loadUpdate,loadUpdate,this);
            configMgr.instance.onloadConfig();
        });
    }
    // 进入游戏
    public override async enterGame() {
        SubGameMgr.inst.MatchRoom(this.gameType, true);
    }
}

SubGameMgr.inst.registerGameMgr(PokerNiuniuGameMgr.inst);


